* {
	box-sizing: border-box;
}
body {
	font-family: "PF Beausans Pro", sans-serif;
	font-weight: 400;
	font-style: normal;
	line-height: 1;
	margin: 0;
	background: #092b46 url('../img/body_bg.jpg') center 0 no-repeat;
	background-size: auto 3976px;
	overflow-x: hidden;
}
.container {
	width: 1200px;
	margin: 0 auto;
}

a {
	text-decoration: none;
}
input {
	background: #00192c;
	border: 1px solid #ffffff;
	border-radius: 25px;
	color: #ffffff;
}
textarea {
	background: #00192c;
	border: 1px solid #ffffff;
	resize: none;
	border-radius: 25px;
	color: #ffffff;
}
.invisble {
	opacity: 0;
}
.line {
	transform: rotate(-3.5deg);
	width: 100%;
	&--plus {
		position: absolute;
		top: 15px;
	}
	&--plus2 {
		position: absolute;
		bottom: 109px;
	}
	&--reviev {
		position: absolute;
		top: 88px;
		.line__two {
			background: #0f3752;
		}
		.line__three {
			background: #0b304a;
		}
	}
	&--reviev2 {
		position: absolute;
		bottom: 70px;
		z-index: 1;
		.line__two {
			background: #0f3752;
		}
		.line__three {
			background: #0b304a;
		}
	}
	&__one {
		width: 100%;
		height: 5px;
		background: #133f5b;
		margin-bottom: 5px;
	}
	&__two {
		width: 100%;
		height: 5px;
		background: #0b2d44;
		margin-bottom: 5px;
	}
	&__three {
		width: 100%;
		height: 5px;
		background: #062033;
	}
}

.razdel {
	position: absolute;
	display: flex;
	width: 25px;
	left: 84px;
	overflow: hidden;
	align-items: center;
	span {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 10px;
		color: #244864;
		width: 22px;
		height: 22px;
		border: 1px solid #244864;
		border-radius: 50%;
		position: relative;
		padding-top: 1.5px;
		padding-left: 1px;
		&:before {
			content: '';
			position: absolute;
			height: 600px;
			width: 1px;
			background: #244864;
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
			bottom: 36px;
		}
		&:after {
			content: '';
			position: absolute;
			top: 36px;
			height: 600px;
			width: 1px;
			background: #244864;
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
		}
	}
	&--header {
		height: 466px;
		top: 257px;
	}
	&--plus {
		height: 465px;
		top: 93px;
	}
	&--tarif {
		height: 1161px;
		top: -13px;
	}
	&--review {
		height: 521px;
		top: -54px;
	}
	&--contacts {
		height: 607px;
		top: -51px;
	}
}
.slick-dots {
	text-align-last: left;
	bottom: -20px;
	left: 0;

	padding-left: 200px;
	z-index: 20;
	li {
		width: auto;
		height: auto;
		margin: 0 2.5px;
		&.slick-active {
			button {
				background: transparent;
				border-color: #cb242c;
				width: 12px;
				height: 12px;
			}
		}
		button {
			width: 8px;
			height: 8px;
			padding: 0;
			border: 2px solid transparent;
			background: #707c85;
			border-radius: 50%;
			&:before {
				display: none;
			}
		}
	}
}

.header {
	//background: url('../img/header-bg.jpg') 0 0 no-repeat;
	height: 907px;
	&__wrapper {
		position: relative;
	}
	&__head {
		display: flex;
		justify-content: space-between;
		padding-top: 30px;
	}
	&__title {
		@include thin;
		font-size: 30px;
		color: #cb242c;
		margin-bottom: 29px;
		margin-top: 28px;
		padding-left: 200px;
	}
    &__mobile {
      display: none;
    }
}
.logo {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 450px;
	padding-left: 15px;
	&__link {
		display: flex;
		//align-items: center;
	}
	&__img {
		border: none;
		width: 167px;
		height: 176px;
	}
	&__wr {
		display: flex;
		flex-direction: column;
		padding-left: 21px;
	}
	&__text-jpg {
		margin-top: 46px;
		border: none;
		width: 253px;
		height: 47px;
	}
	&__text {
		text-transform: uppercase;
		@include light;
		display: inline-block;
		width: 100%;
		text-align: center;
		font-size: 17px;
		color: #308bbb;
		padding-top: 12px;
		letter-spacing: 12px;
	}
}
.menu {
	display: flex;
	//padding-top: 30px;
	position: relative;
	flex-wrap: wrap;
	list-style-type: none;
	padding: 30px 0 0 0;
	margin: 0;
	#magic-line {
		display: inline-block;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 80px;
		height: 4px;
		background: #cb242c;
	}
	&__line {
		position: relative;
		width: 100%;
		height: 4px;
		transform: rotate(-2.99deg);
		margin-top: -12px;
		&:before {
			content: '';
			position: absolute;
			top: -33px;
			left: -78px;
			width: 1102px;
			height: 71px;
			background: url('../img/magicline-bg.png') 0 0 no-repeat;
			transform: rotate(3.4deg);
		}
	}
	&__link {
		display: inline-block;
		padding: 0 13.3px;
		font-size: 19px;
		text-transform: uppercase;
		color: #ffffff;
		position: relative;
		@include rubiklgh;
		position: relative;
		z-index: 100;
		height: 100%;
		&:before {
			position: absolute;
			top: -21px;
			left: 14px;
			color: #2a6a8f;
			font-size: 12px;
		}
		&--1 {
			&:before {
				content: '01';
			}
		}
		&--2 {
			&:before {
				content: '02';
			}
		}
		&--3 {
			&:before {
				content: '03';
			}
		}
		&--4 {
			&:before {
				content: '04';
			}
		}
		&--5 {
			&:before {
				content: '05';
			}
		}
	}
}
.slider {
	padding-left: 200px;
	margin-top: 28px;
	height: 415px;
	overflow: hidden;
	&__text {
		@include light;
		font-size: 15px;
		color: #ffffff;
		line-height: 30px;
	}
	&__item {
		height: 375px;
		overflow: hidden;
	}
	.slick-dots {
		margin-top: 17px;
		display: block;
		position: static;
		padding-left: 0;
	}
}
