		margin-bottom: 17px;
		border-radius: 50%;
		position: relative;
		overflow: hidden;
		cursor: pointer;
		&:before {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background: rgba(0, 0, 0, 0.5);
		}
	}
	&__img {
		border-radius: 50%;
	}
	.slick-current.slick-active {
		outline: none;
		.review-avatar__item {
			&:before {
				background: rgba(0, 0, 0, 0);
			}
		}
	}

	.slick-next {
		top: auto;
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
		bottom: -54px;
		height: 37px;
		width: 18px;
		background: url('../img/avatar_slider-sprite.png') 0 -45px no-repeat;
		&.disabled {
			background-position: -28px -45px;
		}
		&:before {
			display: none;
		}
	}
	.slick-prev {
		top: -38px;
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
		height: 37px;
		width: 18px;
		background: url('../img/avatar_slider-sprite.png') 0 0 no-repeat;
		&.disabled {
			background-position: -28px 0;
		}
		&:before {
			display: none;
		}
	}
}
