		padding-bottom: 3px;
		&--vk {
			&:after {
				content: '';
				position: absolute;
				width: 52px;
				height: 1px;
				bottom: 0;
				right: 0;
				background: #8d99a3;
				border-bottom: 1px solid #425463;
			}
			&:before {
				content: '';
				position: absolute;
				width: 29px;
				height: 17px;
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto 0;
				background: url('../img/social-sprite.png') 0 0 no-repeat;
			}
		}
		&--in {
			margin-left: 36px;
			padding-left: 36px;
			&:after {
				content: '';
				position: absolute;
				width: 119px;
				height: 1px;
				bottom: 0;
				right: 0;
				background: #8d99a3;
				border-bottom: 1px solid #425463;
			}
			&:before {
				content: '';
				position: absolute;
				width: 25px;
				height: 25px;
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto 0;
				background: url('../img/social-sprite.png') -35px 0 no-repeat;
			}
		}
		&--tl {
			margin-left: 22px;
			&:after {
				content: '';
				position: absolute;
				width: 70px;
				height: 1px;
				bottom: 0;
				right: 0;
				background: #8d99a3;
				border-bottom: 1px solid #425463;
			}
			&:before {
				content: '';
				position: absolute;
				width: 25px;
				height: 25px;
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto 0;
				background: url('../img/social-sprite.png') -64px -1px no-repeat;
			}
		}
	}
}

.footer {
	margin-top: 85px;
	padding-bottom: 68px;
	&__wrapper {
		display: flex;
		justify-content: space-between;
		padding-right: 16px;
	}
	&__copy {
		font-size: 15px;
		color: #2a516e;
		padding-left: 120px;
		line-height: 15px;
		position: relative;
		&:before {
			height: 22px;
			width: 22px;
			border-radius: 50%;
			content: 'c';
			position: absolute;
			top: 0;
			bottom: 0;
			margin-top: auto;
			margin-bottom: auto;
			left: 84px;
			border: 1px solid #2c526e;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 12px;
		}
	}
	&__text {
		font-size: 15px;
		color: #2a516e;
	}
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
  z-index: 100;
	display: none;
}
.modal {
	position: fixed;
	top: 0;
	left: 0;
    right: 0;
    bottom: 0;
	margin: auto auto;
	justify-content: center;
	align-items: center;
	//background: rgba(0, 0, 0, 0.5);
	display: none;
    max-width: 800px;
    max-height: 485px;
    z-index: 1000;
	&__wrapper {
		background: #0c354c;
		border: 5px solid #061c30;
        position: relative;
        width: 100%;
	}
	&__close {
		position: absolute;
		right: 10px;
		top: 10px;
		width: 37px;
		height: 37px;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #cb242c;
		cursor: pointer;
		span {
			font-size: 33px;
			line-height: 33px;
		}
	}
	&__title {
		margin-top: 20px;
		margin-bottom: 20px;
		font-size: 30px;
		@include thin;
		color: #cb242c;
		padding: 0 20px;
		text-align: center;
	}
	&__text {
		padding: 0 25px;
		margin-bottom: 30px;
		span {
			font-size: 22px;
			color: #ffffff;
		}
	}
}
.modal-form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 30px 50px;
	width: 100%;
	&__input {
		width: 100%;
		margin-bottom: 20px;
		height: 52px;
		padding: 0 33px;
		line-height: 1;
		&--disabled {
			background: #2d3a41;
		}
	}
	&__sum {
		font-weight: 100;
		font-size: 32px;
		color: #cb242c;
		margin-bottom: 30px;
	}
	&__button {
		background: url(../img/button__bg.png) center no-repeat;
		display: -ms-flexbox;
		display: flex;
		width: 122px;
		height: 47px;
		-ms-flex-align: center;
		align-items: center;
		-ms-flex-pack: center;
		justify-content: center;
		font-size: 18px;
		color: #ffffff;
		border-radius: 28px;
		transition: all .3s;
		background-size: cover;
		overflow: hidden;
		border: none;
		cursor: pointer;
		&:hover {
			background: url(../img/button__bg--hover.png) center no-repeat;
			background-size: cover;
			width: 128px;
			height: 50px;
			font-size: 19px;
			margin-bottom: -3px;

		}
	}
}

